<template>
  <div class="panorama">
    <a-layout>
      <a-layout-header
        >全景图展示（{{ btnList[type] }}）
        <a-button type="primary" @click="changeViewer">{{
          btnList[btnKey]
        }}</a-button>
      </a-layout-header>
      <a-layout-content>
        <div id="panorama-item"></div>
      </a-layout-content>
    </a-layout>
  </div>
</template>
<script setup>
import { Viewer } from "photo-sphere-viewer";
import { CubemapAdapter } from "photo-sphere-viewer/dist/adapters/cubemap";
import "photo-sphere-viewer/dist/photo-sphere-viewer.css";
import { computed, onMounted, reactive, ref } from "vue";
let viewer = ref(null);
const btnList = reactive({
  1: "球体全景图",
  2: "立方体全景图",
});
let type = ref(1);
const btnKey = computed(() => {
  for (const key in btnList) {
    if (type.value != key) {
      return key;
    }
  }
});
onMounted(() => {
  loadViewer();
});
function loadViewer() {
  if (type.value == 1) {
    viewer = new Viewer({
      container: document.getElementById("panorama-item"),
      panorama: require("@/assets/img/panorama/item2.jpg"),
      size: {
        width: "100%",
        height: "100%",
      },
      fisheye: true,
      //具体参数可查看官网:https://photo-sphere-viewer.js.org/guide/
    });
  } else {
    viewer = new Viewer({
      container: document.getElementById("panorama-item"),
      panorama: {
        left: require("@/assets/img/panorama/5.jpg"),
        front: require("@/assets/img/panorama/1.jpg"),
        right: require("@/assets/img/panorama/4.jpg"),
        back: require("@/assets/img/panorama/3.jpg"),
        top: require("@/assets/img/panorama/6.jpg"),
        bottom: require("@/assets/img/panorama/2.jpg"),
      },
      adapter: CubemapAdapter,
      size: {
        width: "100%",
        height: "100%",
      },
      fisheye: true,
    });
  }
}
const changeViewer = () => {
  if (viewer) {
    viewer.destroy();
  }
  type.value = btnKey.value;
  loadViewer();
};
</script> 
<style lang="scss" scoped>
.panorama {
  width: 100%;
  height: 100%;
  .ant-layout {
    height: 100%;
    .ant-layout-header {
      // background-color: rgb(199, 204, 202);
      background-image: linear-gradient(to right, #bcc2ccb6, #375e99aa);
      color: #061720;
      font-size: 20px;
      font-weight: 550;
      .ant-btn {
        position: absolute;
        top: 16px;
        right: 20px;
      }
    }
    .ant-layout-content {
      flex-grow: 1;
    }
  }
}
</style>